.heading_info{
    margin-bottom: 2%;
}


.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me{
    width:100%;
    aspect-ratio: 1;
    border-radius: 2rem;
    background: linear-gradient(45deg, var(--color-primary), transparent);
    display:grid;
    place-items:center;

}

.about__me__img{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg); 
    transition: var(--transition);
}

.about__me__img:hover{
    transform: rotate(0deg);
    
}


.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
    margin-bottom: 3%;
    margin-top: 3%;
}

.about__card{
    background: rgba(1,1,1,0.5);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.805), -5px -5px 10px rgba(255, 255, 255, 0.115);
}

.about__icon{
    /* color: var(--color-primary); */
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.adjust_icon{
    margin-top: 0.3rem;
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: 0.95rem;
}

.about__card small{
    font-size: 0.75rem;
    color: var(--color-light);
}
.about__container p{
    margin: 2rem 0  2.6rem;
}